<template>
  <div class="bg">
    <div class="title">项目资金方进度</div>
    <article class="person_main">
      <div>
        <div>
          <DatePicker
            v-model="formValidate.progressDate"
            type="date"
            placeholder="选择时间"
            :editable="false"
            value="yyyy-MM-dd HH:mm:ss"
            @on-change="formValidate.progressDate=$event;"
            style="width: 200px;margin-right:20px;float: left;"
          ></DatePicker>
          <Input
            v-model="formValidate.address"
            placeholder="地址"
            style="width: 200px;margin-right: 10px;"
          />
          <br>
          <br>
          <Input
            v-model="formValidate.content"
            placeholder="内容"
            style="width: 100%; margin-right: 10px;"
             type="textarea"
          />
        </div>
        <div style="clear: both;margin-bottom: 20px;"></div>
        <div>
          <Upload
            style="float:left; margin-right: 10px;"
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="picture"
            ref="picture"
          >
            <Button icon="ios-cloud-upload-outline">上传图片</Button>
          </Upload>
          <Upload
            style="float:left; margin-right: 10px;"
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="file"
            ref="file"
          >
            <Button icon="ios-cloud-upload-outline">上传文件</Button>
          </Upload>
          <!-- <Upload
            style="float:left; margin-right: 10px;"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="video"
            ref="video"
          >
            <Button icon="ios-cloud-upload-outline">上传视频</Button>
          </Upload>
          <Upload
            style="float:left; margin-right: 10px;"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="audio"
            ref="audio"
          >
            <Button icon="ios-cloud-upload-outline">上传音频</Button>
          </Upload>-->
        </div>
        <div style="clear: both;margin-bottom: 10px;"></div>
        <div style="font-size:12px;color: #dc6969;">*只会添加最后一次上传的图片或文件</div>
        <br />
        <div>
          <Button type="primary" @click="clickAdd">添加新进度</Button>
        </div>
        <Divider />
      </div>
      <div style="clear: both;"></div>
      <div style="margin-top:30px;">
        <Timeline>
          <Timeline-Item v-for="(item, index) in data" :key="index">
            <p class="Titime">{{item.progressDate.split(' ')[0]}}</p>
            <p class="Tititle">{{item.content}}</p>
            <p class="Ticontent">{{item.address}}</p>
            <p class="Ticontent">
              <img :preview="index" :src="imgUrl+item.picture" alt />
              <br />
              <Button v-if="item.picture!=''" type="primary" @click="clickOpen(item.picture)">查看图片</Button>
              <Button v-if="item.file!=''" type="primary" @click="clickOpen(item.file)">查看文件</Button>
              <Button v-if="item.video!=''" type="primary" @click="clickOpen(item.video)">查看视频</Button>
              <Button v-if="item.audio!=''" type="primary" @click="clickOpen(item.audio)">查看音频</Button>
            </p>
            <p class="Ticontent">
              <Poptip confirm title="是否要删除项目进度" @on-ok="clickShow(item.id)">
                <Button type="error">删除进度</Button>
              </Poptip>
            </p>
          </Timeline-Item>
        </Timeline>
      </div>
    </article>
  </div>
</template>
<script>
import { fileUploadUrl, imgUrl } from "../../../utils";
import {
  projectRate,
  projectRateAdd,
  projectRateDelete
} from "../../../api/project";
import { messages } from "../../../utils";
import {
  Timeline,
  Button,
  TimelineItem,
  Poptip,
  Upload,
  Divider,
  DatePicker,
  Input
} from "iview";
export default {
  components: {
    [Input.name]: Input,
    [Timeline.name]: Timeline,
    [TimelineItem.name]: TimelineItem,
    [Poptip.name]: Poptip,
    [Upload.name]: Upload,
    [Divider.name]: Divider,
    DatePicker,
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //搜索配置
      seachConfig: {
        pageNo: 1,
        pageTotal: 1
      },
      formValidate: {
        projectCode: this.$route.params.code,
        type: "2",
        progressDate: "",
        address: "",
        content: "",
        file: "",
        video: "",
        audio: "",
        picture: ""
      },
      data: []
    };
  },
  methods: {
    file(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.file = res.data.url;
      }
    },
    video(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.video = res.data.url;
      }
    },
    audio(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.audio = res.data.url;
      }
    },
    picture(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.picture = res.data.url;
      }
    },
    // 添加进度
    clickAdd() {
      projectRateAdd(this.formValidate).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`进度添加成功`);
          //将文件上传复位
          this.formValidate = {
            projectCode: this.$route.params.code,
            type: "2",
            progressDate: "",
            address: "",
            content: "",
            file: "",
            video: "",
            audio: "",
            picture: ""
          };
          this.$refs.file.clearFiles();
          this.$refs.picture.clearFiles();
          // this.$refs.video.clearFiles();
          // this.$refs.audio.clearFiles();
          // 重新获取列表
          this.getData();
        }
      });
    },
    //获取数据
    getData() {
      projectRate({
        code: this.$route.params.code,
        pageNo: this.seachConfig.pageNo,
        type: 2
      }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.data = res.data.data.rows;
          this.seachConfig.pageTotal = res.data.data.total;
        }
      });
    },
    //点击打开详情
    clickOpen(url) {
      window.open(imgUrl + url);
    },
    //设置隐藏
    clickShow(id) {
      projectRateDelete({ id }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`删除成功`);
          this.getData();
        }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
